<template>
    <!-- Button trigger modal -->
    <button type="button" class="btn btn-primary" @click="openModal">
      Launch demo modal
    </button>
  
    <!-- Modal -->
    <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">Modal title</h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
          <div class="modal-body">
            <!-- Modal Body Content -->
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
            <button type="button" class="btn btn-primary" @click="saveChanges">Save changes</button>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  // Uncomment the following line if you're using Vue's Composition API
  // import { ref } from 'vue';
//   import { Modal } from 'bootstrap';
  import * as bootstrap from 'bootstrap';

  export default {
    data() {
      return {
        modal: null
      };
    },
    mounted() {
      // Initialize modal when the component is mounted
      this.modal = new bootstrap.Modal(document.getElementById('exampleModal'), {
        // Optional: specify options here
      });
    },
    methods: {
      openModal() {
        // Show the modal
        this.modal.show();
      },
      saveChanges() {
        console.log("Successfully saved");
        // You can add your save logic here
        this.modal.hide(); // This will hide the modal after saving
      }
    }
  };
  </script>
  