<script>
// require('@fullcalendar/core/main.min.css')

import FullCalendar from '@fullcalendar/vue3'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import interactionPlugin from '@fullcalendar/interaction'
import listPlugin from '@fullcalendar/list'

import axios from 'axios';
// import { INITIAL_EVENTS, createEventId } from './event-utils'
import bootstrap5Plugin from '@fullcalendar/bootstrap5'
import * as bootstrap from 'bootstrap';


let eventGuid = 0
export function createEventId() {
  return String(eventGuid++)
}

function formatDatetime(datetimeStr) {
  return datetimeStr.replace('T', ' ').replace(/\+.*$/, '');
}

function updateErrorMessage(message) {
  const errorMessageElement = document.getElementById('errorMessage');
  errorMessageElement.textContent = message;
}

export default {
  components: {
    FullCalendar // make the <FullCalendar> tag available
  },

  data: function () {
    return {
      modal: null,
      deletemodal: null,
      errorModal: null,
      // newEventTitle: '',
      nameInput: '',
      lastnameInput: '',
      emailInput: '',

      calendarOptions: {
        //1 day only
        selectAllow: function (selectionInfo) {
          let startDate = selectionInfo.start;
          let endDate = selectionInfo.end;
          endDate.setSeconds(endDate.getSeconds() - 1);  // allow full day selection
          if (startDate.getDate() === endDate.getDate()) {
            return true;
          } else {
            return false;
          }
        },
        plugins: [
          bootstrap5Plugin,
          listPlugin,
          dayGridPlugin,
          timeGridPlugin,
          interactionPlugin // needed for dateClick
        ],
        headerToolbar: {
          left: 'prev,next today',
          center: 'title',
          right: 'dayGridMonth,timeGridWeek,timeGridDay,listMonth'
        },
        themeSystem: 'bootstrap5', // important!
        initialView: 'dayGridMonth',
        // initialEvents: INITIAL_EVENTS, // alternatively, use the `events` setting to fetch from a feed
        editable: true,
        selectable: true,
        selectMirror: true,
        dayMaxEvents: true,
        weekends: true,
        select: this.handleDateSelect,
        eventClick: this.deleteItem,
        eventsSet: this.handleEvents,
        eventDrop: this.handleEventDrop, // Add eventDrop handler
        eventResize: this.handleEventResize,
        eventDisplay: 'block'
        /* you can update a remote database when these fire:
        eventAdd:
        eventChange:
        eventRemove:
        */
      },
      allevents: [],
    }
  },

  mounted() {
    this.fetchEvents();
    this.modal = new bootstrap.Modal(document.getElementById('exampleModal'), {
      // Optional: specify options here
    });
    this.deletemodal = new bootstrap.Modal(document.getElementById('deleteModal'), {
      // Optional: specify options here
    });
    
    this.errorModal = new bootstrap.Modal(document.getElementById('errorModal'), {
      // Optional: specify options here
    });
  },

  methods: {

    handleWeekendsToggle() {
      this.calendarOptions.weekends = !this.calendarOptions.weekends // update a property
    },

    fetchEvents() {
      // Fetch events from local storage
      let eventsFromLocalStorage = JSON.parse(localStorage.getItem('events')) || [];

      // Fetch events from the API
      axios.get('http://localhost:8000/api/events')
        .then(response => {
          let eventsFromAPI = response.data.filter(event => event.user === 'admin').map(event => {
            let color;
            switch (event.user) {
              case 'clientApproval':
                color = '#007FFF';
                break;
              case 'clientApproved':
                color = '#FF9E00';
                break;
              case 'admin':
                color = '#FF2D00';
                break;
              default:
                // Handle default case if needed
                break;
            }
            return {
              id: event.id,
              title: event.title,
              email: event.email,
              user: event.user,
              start: event.start,
              end: event.end,
              allDay: event.allDay,
              color: color // Assign color based on user type

            };

          });

          // Update local events with API events and update user if IDs match
          eventsFromLocalStorage.forEach(localEvent => {

            let correspondingAPIEvent = response.data.find(apiEvent => apiEvent.id === localEvent.id);
            if (correspondingAPIEvent) {

              if (correspondingAPIEvent.user !== localEvent.user) {
                console.log("Delete Approved clients:::", correspondingAPIEvent.id, correspondingAPIEvent.user, "  :  ", localEvent.id, localEvent.user);
                const updatedEvents = eventsFromLocalStorage.filter(event => event.id !== localEvent.id);
                console.log('Updated events after deletion:', updatedEvents);
                // Store the updated events back into local storage
                localStorage.setItem('events', JSON.stringify(updatedEvents));
                this.fetchEvents();
              }

            }

            if (!correspondingAPIEvent && localEvent) {
              const updatedEvents = eventsFromLocalStorage.filter(event => event.id !== localEvent.id);
              // Store the updated events back into local storage
              localStorage.setItem('events', JSON.stringify(updatedEvents));
              this.fetchEvents();
            }
          });

          // console.log('Events before deletion:', eventsFromLocalStorage);

          let allEvents = eventsFromLocalStorage.concat(eventsFromAPI);

          this.calendarOptions.events = allEvents; // Directly assign events to calendarOptions

        })
        .catch(error => {
          console.error('Error fetching events:', error);
        });
    },



    //ADD EVENT
    handleDateSelect(selectInfo) {
      const moment = require('moment');
      // Get the current time in HH:mm:ss format
      const currentTime = moment().format('HH:mm:ss');
      let startSelectedDate = selectInfo.startStr + " " + currentTime;

      axios.get('http://localhost:8000/api/events')
        .then(response => {
          let canAddEvent = true;
          response.data.forEach(event => {
            const moment = require('moment');
            // Combine the current time with selectInfo.startStr
            let startSelectedDateNoTime = moment(selectInfo.startStr).format('YYYY-MM-DD');
            let EndSelectedDateNoTime = moment(event.start).format('YYYY-MM-DD');
            // console.log(event.user, event.allDay, "startSelected", startSelectedDateNoTime,  "EndSelected", EndSelectedDateNoTime);
            console.log(event.user, "\n startSelectedDate", startSelectedDate, "\n event.start", event.start, "\n startSelectedDate", startSelectedDate, "\n event.end", event.end);

            // Check if the admin has added an event that overlaps with the selected time range
            if (event.user === 'admin' && event.allDay === 1 && startSelectedDateNoTime === EndSelectedDateNoTime || event.user === 'admin' && startSelectedDate >= event.start && startSelectedDate <= event.end) {
              canAddEvent = false;
              // console.log("The schedule is busy day of admin. Please select another date");
              // this.errorModal.show();
              updateErrorMessage("The schedule is busy on this day. Please select another date");
              this.errorModal.show(); 
            }
          });

          if (canAddEvent) {
            this.modal.show();
            this.selectedInfo = selectInfo;
            console.log("YOU CAN NOW ADD");
          }
        })
        .catch(error => {
          console.error('Error fetching events:', error);
        });
    },



    saveChanges(selectedInfo) {
      this.newEventTitle = this.nameInput + ' ' + this.lastnameInput;
      if (this.newEventTitle.trim() !== '') {

        let calendarApi = selectedInfo.view.calendar;
        calendarApi.unselect() // clear date selection

        // Format start and end dates
        let start = formatDatetime(selectedInfo.startStr);
        let end = formatDatetime(selectedInfo.endStr);

        axios.post('http://localhost:8000/api/events', {
          title: this.newEventTitle,
          email: this.emailInput,
          start: start,
          end: end,
          user: 'clientApproval',
          allDay: selectedInfo.allDay
        })
          .then(response => {
            // Hide the modal
            this.modal.hide();
            // Handle success
            // console.log('Event added:', response.data);

            this.allevents.push(response.data);

            let newEvent = {
              id: response.data.id,
              title: response.data.title,
              email: response.data.email,
              user: response.data.user,
              start: response.data.start,
              end: response.data.end,
              allDay: response.data.allDay,

            };

            this.allevents.push(newEvent);
            // Get existing events from local storage or initialize an empty array
            let events = JSON.parse(localStorage.getItem('events')) || [];
            events.push(newEvent); // Append the new event to existing events
            localStorage.setItem('events', JSON.stringify(events)); // Set the updated events array to local storage

            calendarApi.addEvent(newEvent);
            // Fetch events again to update the calendar
            this.fetchEvents();

            // this.newEventTitle = '';
            this.nameInput = '';
            this.lastnameInput = '';
            this.emailInput = '';

          })
          .catch(error => {
            // Handle error
            console.error('Error adding event:', error.response.data);
          });
      } else {
        // Alert the user if the input field is empty
        alert('Please enter a title for the event');
      }
    },


    deleteItem(clickInfo) {
      // console.log(clickInfo.event.id, clickInfo.event);
      const user = clickInfo.event.extendedProps && clickInfo.event.extendedProps.user;
      // console.log(user);

      if(user == 'admin'){
        // console.log("YOU CANNOT DELETE THIS IS FROM ADMIN");
        updateErrorMessage("Oops! You can't delete admin Schedule.");
        this.errorModal.show();

      }else{
        this.deletemodal.show();
        this.clickedInfo = clickInfo;
      }
    },

    deleteItemModal(clickedInfo) {

      axios.delete(`http://localhost:8000/api/events/${clickedInfo.event.id}`)
        .then(response => {
          this.deletemodal.hide();
          console.log("Succesfully Deleted: " + response.data + clickedInfo.event.id);
          // Fetch events from local storage
          let events = JSON.parse(localStorage.getItem('events')) || [];
          // Filter out the event to be deleted
          const updatedEvents = events.filter(event => String(event.id) !== String(clickedInfo.event.id));
          // Store the updated events back into local storage
          localStorage.setItem('events', JSON.stringify(updatedEvents));
          this.fetchEvents();
        })
        .catch(error => {

          console.error('Error deleting event:', error);
        });

    },

    handleEventDrop(info) {
  const moment = require('moment');
  let startSelectedDate = moment(info.event.start).format('YYYY-MM-DD HH:mm:ss');
  let endSelectedDate = moment(info.event.end).format('YYYY-MM-DD HH:mm:ss');

  axios.get('http://localhost:8000/api/events')
    .then(response => {
      let canMoveEvent = true;
      response.data.forEach(event => {
        let eventStart = moment(event.start).format('YYYY-MM-DD HH:mm:ss');
        let eventEnd = moment(event.end).format('YYYY-MM-DD HH:mm:ss');

        // Check if the admin has added an event that overlaps with the selected time range
        if (event.user === 'admin' && event.allDay === 1 && startSelectedDate === eventStart || 
            event.user === 'admin' && startSelectedDate >= eventStart && endSelectedDate <= eventEnd) {
          canMoveEvent = false;
          // console.log("SHOW MODAL ALREADY ADDED BY ADMIN");
          updateErrorMessage("Oops! You can't drop, admin  already have Schedule.");
          
          this.errorModal.show();
        }
      });

      if (canMoveEvent) {

      const user = info.event.extendedProps && info.event.extendedProps.user;
      // console.log(user);

      if(user == 'admin'){
        updateErrorMessage("Oops! You can't drop the admin Schedule.");
          this.errorModal.show();
        this.fetchEvents();
        return;
      }


      const eventId = info.event.id;
      const allDay = info.event.allDay;
      const start_formatdate = formatDatetime(info.event.startStr);
      let end_formatdate = formatDatetime(info.event.endStr);

      const moment = require('moment');
      // If end date is empty, set it to start date
      if (end_formatdate === "") {
        end_formatdate = moment(start_formatdate).add(1, 'hours').format('YYYY-MM-DD HH:mm:ss');
      }
      if(allDay){
        end_formatdate = moment(start_formatdate).add(24, 'hours').format('YYYY-MM-DD HH:mm:ss');
        console.log("allDay", allDay);
      }
      axios.put(`http://localhost:8000/api/events/${eventId}`, {
        start: start_formatdate,
        end: end_formatdate,
        allDay: allDay
      })
        .then(response => {
          console.log('Event updated successfully:', response.data);

          const events = JSON.parse(localStorage.getItem('events')) || [];
          events.forEach((event, index) => {
            if (event.id == eventId) {
              // Update the event's details
              events[index] = { ...event, start: start_formatdate, end: end_formatdate, allDay: allDay };
              // console.log("Updated Event ID:", event.id, "with new start and end dates.");
            }
          });

          // Save the updated events array back to local storage
          localStorage.setItem('events', JSON.stringify(events));
        })
        .catch(error => {
          console.error('Error updating event:', error.response.data);
        });

      } else {
        // Logic to revert the event drop if needed
        info.revert();
      }
    })
    .catch(error => {
      console.error('Error fetching events:', error);
    });
},

    // Function to handle resize event
    handleEventResize(info) {
      const user = info.event.extendedProps && info.event.extendedProps.user;
      if(user == 'admin'){
        updateErrorMessage("Oops! You can't resize admin Schedule.");
        this.errorModal.show();
        info.revert();
        return;
      }

      const eventId = info.event.id;
      const start = formatDatetime(info.event.startStr);
      const end = formatDatetime(info.event.endStr);

      console.log("handleEventResize : ", eventId, "start ", start, "  end ", end);

      axios.put(`http://localhost:8000/api/events/${eventId}`, {
        start: start,
        end: end,
        allDay: false
      })
        .then(response => {
          console.log('Event updated Resize:', response.data);
        })
        .catch(error => {
          console.error('Error updating event:', error.response.data);
        });
    },

    handleEvents(events) {
      this.allevents = events;
    }
  },
}
</script>



<template>
  <div class='demo-app'>
    <div class='demo-app-sidebar' style="display: flex; flex-direction: column; justify-content: space-between;">
      <div>
        <div class="text-center">
          <img src="../assets/icon.png" alt="Ears Nose and Throat" style="width: 160px; height: auto; "
            class="img-fluid">
        </div>
        <div style="margin: 50px 5px 10px 5px ;">
          <h2>Instructions</h2>
          <ul style="text-align: left;">
            <li>Select dates and you will be prompted to create a new event</li>
            <li>Drag, drop, and resize events</li>
            <li>Click an event to delete it</li>
          </ul>
        </div>
      </div>

      <!-- Admin Login -->
      <div class="text-center" style="margin: 10px 5px 10px 5px ;">
        <router-link :to="{ name: 'about' }">about</router-link>
        <router-view />
      </div>

      <!-- Admin Login -->
      <div class="text-center" style="margin: 10px 5px 10px 5px ;">
        <router-link :to="{ name: 'login' }">Admin Login</router-link>
        <router-view />
      </div>
    </div>


    <div class='demo-app-main'>
      <FullCalendar class='demo-app-calendar' :options='calendarOptions'>
        <template v-slot:eventContent='arg'>
          <b>{{ arg.start }}</b>
          <i>{{ arg.event.title }}</i>
        </template>
      </FullCalendar>

      <!-- Modal -->
      <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered">
          <div class="modal-content">
            <div class="modal-header bg-primary text-white">
              <h5 class="modal-title" id="exampleModalLabel">Create Schedule</h5>
              <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <!-- Modal Body Content -->
            <div class="modal-body">
              <!-- <div class="mb-3">
                  <input type="text" class="form-control" id="newEventTitle" v-model="newEventTitle" placeholder="Event Title">
                </div> -->
              <div class="mb-3">
                <input type="text" class="form-control" id="nameInput" v-model="nameInput" placeholder="Name">
              </div>
              <div class="mb-3">
                <input type="text" class="form-control" id="lastnameInput" v-model="lastnameInput"
                  placeholder="Lastname">
              </div>
              <div class="mb-3">
                <input type="email" class="form-control" id="emailInput" v-model="emailInput" placeholder="Email">
              </div>
              <!-- Rest of your modal content -->
            </div>

            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
              <button type="button" class="btn btn-primary" @click="saveChanges(selectedInfo)">Save changes</button>
            </div>
          </div>
        </div>
      </div>

      <!-- Delete -->
      <div class="modal fade" id="deleteModal" tabindex="-1" aria-labelledby="deleteModal" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered">
          <div class="modal-content">
            <div class="modal-header bg-danger text-white">
              <h5 class="modal-title">Delete</h5>
              <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div class="modal-body">
              <p>Are you sure you want to delete?</p>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
              <button type="button" class="btn btn-danger" @click="deleteItemModal(clickedInfo)">Delete</button>
            </div>
          </div>
        </div>
      </div>

            <!-- Error -->
        <div class="modal fade" id="errorModal" tabindex="-1" aria-labelledby="errorModal" aria-hidden="true">
          <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
              <div class="modal-header bg-danger text-white">
                <h5 class="modal-title bi-exclamation-triangle-fill"> Error</h5>
                <button type="button" class="btn-close btn-close-white" data-bs-dismiss="modal" aria-label="Close"></button>
              </div>
              <div class="modal-body">
                <p><i class="bi"></i> <span id="errorMessage"></span></p>
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-danger" data-bs-dismiss="modal">Close</button>
              </div>
            </div>
          </div>
        </div>

    </div>
  </div>
</template>



